 import axios from 'axios';
 import { notification } from 'ant-design-vue';

 let BASE_URL = ''

 BASE_URL = process.env.NODE_ENV == 'development' ? 'http://localhost:4041/api/' : '/api/'

 /**
  * 
  * 
  */
 // const tip = msg => {
 //   Toast({
 //     message: msg,
 //     duration: 1000,
 //     forbidClick: true
 //   });
 // }
 /**
  * 
  * @param {Number} status 
  */
 const errorHandle = (status, other) => {
   // 
   switch (status) {
     // 401: 
     case 401:
      notification.warning('Please login first!')
       // toLogin();
       break;
     // 403 token
     // token
     case 403:
      notification.warning('Please login first!')
       // localStorage.removeItem('token');
       // store.commit('loginSuccess', null);
       // setTimeout(() => {
       //   toLogin();
       // }, 1000);
       break;
     // 404
     case 404:
      notification.warning('Can`t find the source!')
       break;
     case 422:
       for (const msg in other) {
        notification.warning(`${msg} ${other[msg]}`)
       }
       break;
     case 500:
      notification.warning('Service is busy, please try later!')
       break;
     default:
       console.log(other);
   }
 }
 
 // axios
 const instance = axios.create({ timeout: 1000 * 12 });
 // post
 // instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
 // instance.defaults.headers.post['Content-Type'] = 'application/json';
 instance.defaults.baseURL = BASE_URL;
//  instance.defaults.headers.common['X-CSRF-Token'] = document.querySelector('meta[name="csrf-token"]').getAttribute('content');
 instance.defaults.headers.common.Accept = 'application/json'
 
 /**
  * 
  * tokentoken
  */
 // instance.interceptors.request.use(
 //   config => {
 //     // token
 //     // tokentokentoken
 //     // token
 //     // 
 //     const token = store.state.token;
 //     token && (config.headers.Authorization = token);
 //     return config;
 //   },
 //   error => Promise.error(error))
 
 // 
 instance.interceptors.response.use(
   // 
   (res) => ((res.status >= 200 && res.status < 300) ? Promise.resolve(res.data) : Promise.reject(res)),
   // 
   (error) => {
     const { response } = error;
     if (response) {
       // 2xx
       errorHandle(response.status, response.data);
       return Promise.reject(response);
     }
     // 
     // eg:statenetwork
     // networkapp.vue
     // 
    //  store.commit('changeNetwork', false);
   },
 );
 
 export default instance;
 